<template>
	<div class="SetNotesAndLabels Page">
		<div class="SetNotesAndLabelshead">
			<i class="icon setgoback icon-chevron-left" @click="$store.commit('showOrHidden',['setNotesAndLabelsPage',false])"></i>
			<span class="settext"></span>
			<span v-if="setFinish" class="setFinishdone" @click="sendsetmsg"></span>
			<span v-else class="setFinish"></span>
		</div>
		
		<div class="setsetmsg">
			<div>
				<p></p>
				<input ref="name" type="text" :value="infomsg.name? infomsg.name : infomsg.nickname" @input="setinput">
			</div>
			<div>
				<p></p>
				<input ref="label" @input="setinput" v-if="infomsg.label" :value="infomsg.label" >
				<input ref="label" @input="setinput" v-else placeholder="" type="text">
			</div>
			<div>
				<p></p>
				<div class="border-bottom">
					<div  v-if="infomsg.phoneNumber">
						<input class="phoneInput" v-for="(v,i) in infomsg.phoneNumber" :key="i" @input="setinput" :value="v" type="search">
						<input class="phoneInput" :key="infomsg.phoneNumber.length" type="search" @input="setinput" placeholder="">
					</div>
					<input class="phoneInput" @input="setinput" v-else placeholder="" type="search">
				</div>
			</div>
			<div>
				<p></p>
				<div  class="border-bottom">
					<input ref="describe" v-if="infomsg.describe" :value="infomsg.describe" type="text" @input="setinput">
					<input ref="describe" v-else placeholder="" @input="setinput">
					<span >400</span>
				</div>
			</div>
			<div>
				<p></p>
				<input ref="picture" v-if="infomsg.picture" :value="infomsg.picture" type="file">
				<input ref="picture" v-else placeholder=""  type="file">
			</div>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				setFinish:false,
				setmsg:{},
				infomsg: null,
			};
		},
		created(){
			this.infomsg = this.$store.state.chatPageMsg;
		},
		methods: {
			sendsetmsg(){
				let phoneNumber = []
				let phoneInputs = document.getElementsByClassName('phoneInput');
				console.log(phoneInputs)
				for(var i=0; i<phoneInputs.length; i++){
					console.log(i,phoneInputs[i].value)
					if(phoneInputs[i].value){phoneNumber.push(phoneInputs[i].value);}
				}
				// if(this.$refs.phoneNumber.value){phoneNumber.push(this.$refs.phoneNumber.value)}
				
				let setinfomation = {
					weChatId:this.infomsg.weChatId,
					name:this.$refs.name.value,
					label:this.$refs.label.value.split(' '),
					phoneNumber: phoneNumber,
					describe:this.$refs.describe.value.split(' '),
					picture:this.$refs.picture.value
				}
				this.$store.commit('setNotes',setinfomation);
				this.$store.commit('showOrHidden',["setNotesAndLabelsPage",false])//
			},
			setinput(e){
				console.log('',e.target.value);
				//;
				this.setFinish = true;//input
			},
		}
	}
</script>

<style>
	.SetNotesAndLabels{
		z-index: 21;
	}
	.SetNotesAndLabelshead{
		position: sticky;
		top: 0;
		width: 100%;
		display: flex;
		height: 3rem;
		z-index: 2;
		border-bottom: 1px solid rgba(17,27,37,0.1);
		background: rgb(237,237,237);
	}
	
	.setgoback{
		flex: 0 0 3rem;
		height: 3rem;
		line-height: 3rem;
	}
	
	.settext{
		flex: 1;
		height: 3rem;
		line-height: 3rem;
	}
	
	.setFinish,.setFinishdone{
		flex: 0 0 4rem;
		height: 2rem;
		line-height: 2rem;
		background: rgba(12,240,12,.3);
		margin: 0.5rem .5rem;
		border-radius: 0.2rem;
		text-align: center;
	}
	.setFinishdone{
		background: rgb(12,240,12);
	}
	.setsetmsg>div{
		margin-top: 1rem;
	}
	.setsetmsg{
		text-align: left;
		padding: 1rem 1rem 5rem 1rem;
	}
	
	.setsetmsg input{
		display: inline-block;
		height: 3rem;
		width: 18rem;
		line-height: 3rem;
		border: none;
		outline: none;
		padding: 0 0.5rem;
		background: whitesmoke;
		font-size: 1.7rem;
		caret-color: rgb(12,245,12)
	}
	.setsetmsg p{
		margin: 1rem 0;
	}
	.setsetmsg p{
		font-size: 1.5rem;
		color: darkgray;
	}
	.border-bottom{
		border-bottom:  1px solid rgb(17,27,37,0.1);
	}
</style>
