<template>
	<div class="Page">
		<div @click="goback" class="goback">X</div>
		
		<div style="margin-top: 4rem;padding: 1rem;text-align: left;">
			<p class="textt"></p>
			<div class="item">
				<span class="text"></span>
				<input class="input" type="text" placeholder=": " ref="nickname" @input="check">
			</div>
			
			<div class="item">
				<span class="text"></span>
				<input class="input" type="text" placeholder="" @input="check" ref="number">
			</div>

			<div class="error" v-if="error">{{init}}</div>
			
			<div class="item">
				<span class="text"></span>
				<input class="input" type="password" placeholder="" @input="check" ref="password">
			</div>
			
			<div class="register" :class="{'active':ok}" @click="register"></div>
			
			<div class="deal">
				<a href="#"></a>
				<a href="#"></a>
			</div>
			
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
                init:null,
                error:false,
				ok:false,
			};
		},
		methods: {
			goback(){
				this.$store.commit('showOrHidden',['registerPage',false])//
				this.$store.commit('showOrHidden',['loginPage',true])//
			},
			check(){
				let nickname = this.$refs.nickname.value;
				let number = this.$refs.number.value;
				let password = this.$refs.password.value;
				
				if(nickname.length > 0 && password.length > 0 && number.length > 0){
					this.ok = true
				}else{
					this.ok = false
				}
			},
			register(){
			    let data = this.$store.state.data;
			   	for(var i=0; i<data.length; i++){
                    console.log(data[i],this.$refs.number.value);
                    if(data[i].weChatId == this.$refs.number.value){
                        console.log('')
                        this.init = '';
                        this.error = true;
                        return
					}
                }
                this.error = false;
                this.$store.commit('registerUser',[this.$refs.nickname.value,this.$refs.number.value,this.$refs.password.value]);//
                this.$store.commit('showOrHidden',['registerPage',false])//
                this.$store.commit('showOrHidden',['loginPage',true])//
			}
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	.error{
		color: #f00;
	}
	.goback{
		position: absolute;
		top:1rem;
		left: 1rem;
		font-size: 2rem;
	}
	.textt{
		font-size: 2.5rem;
		font-weight: 700;
		margin-bottom: 2rem;
	}
	
	.item{
		height: 4rem;
		line-height: 4rem;	
		border-bottom: 1px solid springgreen;
			
		.text{
			font-size: 2rem;
		}
		
		.input{
			background: rgb(237,237,237);
			border: none;
			outline: none;
			padding-left: 2rem;
			height: 3rem;
			color: rgb(22,240,22);
			font-size: 1.6rem;
		}
	}
	
	.deal{
		margin-top: 2rem;
		text-align: left;
	}
	
	.register,.active{
		text-align: center;
		width: calc(100% - 2rem);
		height: 3rem;
		background: rgba(0,150,0,0.5);
		margin-top: 1rem;
		margin-left: 1rem;
		border-radius: 0.5rem;
		font-size: 2rem;
		line-height: 3rem;
		color: rgba(200,200,200,0.5);
	}
	.active{
		background: rgb(0,150,0);
		color: rgb(200,200,200);
	}
</style>
