<template>
	<div class="box">
		<!--  -->
		<div class="head" >
			<div v-show="showhead">
				<div  class="headLeft">
					<span>{{nowPage[index].text}} <i v-show="index==0">{{unreadAllNumber > 0 ? '(' + unreadAllNumber + ')' : '.'}}</i></span>
				</div>
				<div class="headRight" >
					<i class="icon icon-search" @click="$store.commit('showOrHidden',['addFriendPage',true])"></i>
					<i class="icon icon-plus" @click="$store.commit('showOrHidden',['rightAddPage',true])"></i>
				</div>
			</div>
		</div>
		<div ref="myapp" class="ul" id="ul" @touchstart="ontouchstart" @touchmove="ontouchmove" @touchend="ontouchend">
			<div class="inbox" >
				<!--  -->
				<div class="weixin">
					<WeChatList @click.stop=''></WeChatList>
				</div>
				
				<!--  -->
				<div class="tongxunlu">
					<Addresslist @click.stop=''></Addresslist>
				</div>
				
				<!--  -->
				<div class="faxian" @click.stop=''>
					<FindPage ></FindPage>
				</div>
			</div>
			<!--  -->
			<div class="wode" v-show="show">
				<MainPage></MainPage>
			</div>
		</div>
		
		<!--  -->
		<div class="footer" >
			<div class="footerItem" :class="{'active' : i==index}" v-for="(v,i) in nowPage" :key="i" @click="changeIndex(i)">
				<div>
					<i :class="i == index ? v.active : v.icon" ></i>
					<!--  -->
					<div v-if="i==0">
						<div class="unreadNumberDiv"  v-if="unreadAllNumber > 0">
							<span class="unreadNumber">{{unreadAllNumber}}</span>
						</div>
					</div>
				</div>
				<div class="text">{{v.text}}</div>
			</div>
		</div>
		
		<!--  -->
		<transition name="opacity">
			<RightAddPage v-if="$store.state.rightAddPage"></RightAddPage>
		</transition>
		
		<AddRightMove></AddRightMove>

	</div>
</template>

<script>
	import RightAddPage from './RightAddPage.vue'
	import AddRightMove from './AddRightMove.vue'
	import WeChatList from './WeChatList.vue'
	import Addresslist from './AddressList.vue'
	import FindPage from './FindPage.vue'
	import MainPage from './MainPage.vue'
	export default {
		data() {
			return {
				load: true,
				show: true,
				data: this.$store.state.WeChat, //
				nowPage: [
					{icon:"icon-bubble2 icon",active:"icon-bubble1 icon",text:""},
					{icon:"icon-bubble2 icon",active:"icon-bubble1 icon",text:""},
					{icon:"icon-compass2 icon",active:"icon-bubble1 icon",text:""},
					{icon:"icon-users icon",active:"icon-bubble2 icon",text:""},
				],//icon activetext
				index:0,
				starLeft: 0, //ulleft
				endLeft: 0,//
				touchX: 0, //x
				moveX: null, //
				move: false, //
				ulWidth: null, // ulleft
				liWidth: null,//
				ele: null, //ul
				showhead: true, //
			};
		},
		mounted(){
			let ele = document.getElementById('ul');
			this.ele = ele;
			let width = parseInt( window.getComputedStyle(ele,null).width)
			this.ulWidth = width * 0.75;
			this.liWidth = width * 0.25;
		},
		methods: {
			ontouchstart(e){
				if(this.load){this.ele = document.getElementById('ul');this.load = false}
				this.ele.style.transition = "none;"
				this.starLeft = parseInt( window.getComputedStyle(this.ele,null).left);//ulleft
				this.touchX = e.touches[0].pageX;//
			},
			ontouchmove(e){
				this.move = true;
				this.moveX = e.touches[0].pageX - this.touchX;//

				if((this.index === 2 && this.moveX < 0) || this.index >2){this.showhead = false}

				else{this.showhead = true}
				let left = this.starLeft + this.moveX;//left
				left = left < -this.ulWidth ? -this.ulWidth : left > 0 ? 0 : left; 
				this.ele.style.left = left + 'px';//
				this.endLeft = left; //left
			},
			ontouchend(){
				if(this.move){
					this.move = false;
					this.ele.style.transition = 'all 0.3s linear';
					let index = -Math.abs(Math.floor(this.endLeft / this.liWidth)); // 
					if(index === -3 && this.moveX < 0) { //left
						this.ele.style.left = index * this.liWidth + 'px';
						this.index = 3;
						return
					}
					
					let is = Math.abs(this.endLeft % this.liWidth) > this.liWidth / 2; // 
					let left = 0;
					if(is){
						left = index * this.liWidth;//
						this.index = -index
					}else{
						left = (index+1) * this.liWidth;//
						this.index = -index - 1
					}
					left = left < -this.ulWidth ? -this.ulWidth : left > 0 ? 0 : left;//
					this.ele.style.left = left + 'px';
					this.index = -left / this.liWidth;
					if(this.index == 2){setTimeout(()=>{this.showhead = true},300)}
				}
			},
			changeIndex(i){
				if(this.load){this.ele = document.getElementById('ul');this.load = false}
				this.index = i;//index
				this.ele.style.transition = 'none';//transitionnone
				this.ele.style.left = -i * this.liWidth + 'px';//ulleft
			}
		},
		computed:{
			unreadAllNumber(){//
				let total = 0;
				for(let j=0; j<this.data.length;j++){
					if(!this.data[j].NoDisturbing){ //
						let data = this.data[j].information
						for(let i = data.length-1; i>=0;i--){
							if(data[i].read){break}
							else{total++}
						}
					}
				}
				return total;
			}
		},
		components: {
			AddRightMove,
			RightAddPage,
			WeChatList,
			Addresslist,
			FindPage,
			MainPage
		}
		
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import '../../public/sass/sass';
	
.box{
	text-align: left;
	position: fixed;
	width: 100%;
	height: 100%;
}

.ul{
	position: absolute;
	left: 0px;
	width: 400%;
	height: 100%;
	
	.inbox{
		width: 75%;
		height: 100%;
		display: flex;
		
		.weixin,.tongxunlu,.faxian{
			flex: 1;
			overflow: scroll;
			margin: 3rem 0 4rem 0;
			background: rgb(237,237,237);
		}
		.weixin::-webkit-scrollbar{
			display: none;
		}
	}
	
	.wode{
		position: absolute;
		z-index: 20;
		right: 0;
		top: 0;
		width: 25%;
		height: calc(100% - 3rem - 19px);
		background: rgb(237,237,237);
		margin-bottom:4rem;
		overflow: scroll;
		box-sizing: border-box;
	}
}

.head{
	padding: 0 1rem;
	position: fixed;
	left: 0;
	right: 0;
	height: 3rem;
	z-index: 10;
	background: rgb(247,247,247);
		
	.headLeft{
		display: inline-block;
		height: 3rem;
		span{
			display: inline-block;
			height: 3rem;
			line-height: 3rem;
			font-size: 1.3rem;
		}
	}
	
	.headRight{
		float: right;
		display: inline-block;
		
		.icon{
			display: inline-block;
			height: 3rem;
			width: 2rem;
			margin-left: 1rem;
			line-height: 3rem;
			font-size: 2.2rem;
		}
	}

}

.footer{
	position: fixed;
	display: flex;
	bottom: 0;
	left: 0;
	right: 0;
	height: 4rem;
	z-index: 10;
	background: rgb(247,247,247);
	text-align: center;
	
	.unreadNumberDiv{
		position: absolute;
		top: 0.4rem;
		right: 25%;
		background: red;
		height: 1.5rem;
		min-width: 1rem;
		border-radius: 0.6rem;
		display: inline-block;
		
		.unreadNumber{
			position: relative;
			top: 0.4rem;
			color: white;
			font-size: xx-small!important;
			font-size: 0.8rem;
		}
	}
		
	.footerItem{
		flex: 1;
		position: relative;
			
		.icon{
			display: inline-block;
			width: 100%;
			height: 2.5rem;
			padding-top: 0.3rem;
			box-sizing: border-box;
			font-size: 2.5rem;
			line-height: 2.5rem;
		}
		.text{
			height: 1.5rem;
			line-height: 1.5rem;
		}
	}
	
	.active{
		color: springgreen;
	}
}

</style>
